import React from 'react';
import connect from '@vkontakte/vk-connect';
import {VKMiniAppAPI} from '@vkontakte/vk-mini-apps-api';
import {Panel, View, Div, Button, CellButton, Cell, Avtar, PanelHeader, Group, Header, Spinner} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css';

import Users from './panels/Users';
import Test from './panels/Test';
import Creator from './panels/Creator';

class App extends React.Component {
	constructor(props){
		super(props);

		this.state = {
			activePanel:'home',
			new_test_id:null

		}
	}



	render(){
	return (
		<View activePanel={this.state.activePanel} >
		  <Panel id="home">
			 <PanelHeader>Меню</PanelHeader>
			 <CellButton onClick={()=>this.setState({activePanel:"users"})}>Участники</CellButton>
			 <CellButton onClick={()=>this.setState({activePanel:"creator_t"})}>Создание темы</CellButton>
			  <CellButton onClick={()=>this.setState({activePanel:"test"})}>Создание теста</CellButton>
			 <CellButton>Покупки</CellButton>
			 <CellButton>Ответы учеников</CellButton>
			</Panel>

			<Panel id="users">
			 <Users back={()=>this.setState({activePanel:'home'})}/>
			</Panel>

			<Panel id="creator_t">
       <Creator back={()=>this.setState({activePanel:'home'})} go={(test_id,e)=>this.setState({activePanel:'test', new_test_id:test_id})}/>
			</Panel>
			<Panel id="test">
			 <Test id={this.state.new_test_id} back={()=>this.setState({activePanel:'home'})}/>
			</Panel>
		</View>
	);
  }
}

export default App;
