import React from 'react';
import { Spinner, List, Avatar, Cell, PanelHeaderButton,PanelHeader } from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css';
import {VKMiniAppAPI} from '@vkontakte/vk-mini-apps-api';

import Icon24Back from '@vkontakte/icons/dist/24/back';

var axios = require('axios');

const api = new VKMiniAppAPI();
const token = "6e15e8bc6e15e8bc6e15e8bc326e7adb2a66e156e15e8bc30314e1d8400b2d525b335ce";

class Users extends React.Component {
  constructor(props) {
  super(props);
    this.state = {
      list:null
    }
  }

  componentDidMount(){
    axios.post("https://bot-api-vk.000webhostapp.com/Admin.php",JSON.stringify({type:'get_users'})).then(data=>{
    this.setState({list:data.data})
    })
  }


  render(){
    return(
      <div>
      <PanelHeader left={<PanelHeaderButton onClick={this.props.back}><Icon24Back /></PanelHeaderButton>}>Участники</PanelHeader>
       {this.state.list === null ? <Spinner size="large" style={{marginTop:30}} /> :
         <List>
         {this.state.list.map((data, i) =>
          <Cell key={i} before={<Avatar src={data.response[0].photo_200}/>} href={"https://vk.com/"+data.response[0].screen_name}>{data.response[0].first_name+" "+data.response[0].last_name}</Cell>
         )}
         </List>
       }
      </div>
    )
  }
}
export default Users;
